* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.country-item {
  display: flex;
  align-items: center;
}

.country-item:not(:last-child) {
  margin-bottom: 20px;
}

.country-flag {
  display: block;
  margin-right: 20px;
}

.country-name {
  font-size: 20px;
}

.country-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-info--blod {
  font-size: 20px;
  font-weight: 500;
}

.text-info {
  font-size: 20px;
}
